body{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: Inter;
    background-color: #EFF0F6;
}
*,
*::after,
*::before{
    box-sizing: inherit;
    padding: 0;
    margin: 0;
}
a{
    text-decoration: none;
    color: inherit;
}
ul,
li{
    list-style: none;
}
.container{
    padding-left: 15px;
    padding-right: 15px;
}
